
<template>
  <div class="main_cont">
    <base-header class="pb-6 pb-8 pt-5 pt-md-8 ">
      <!-- Card stats -->
    </base-header>

    <b-container fluid class="mt--7">
      <b-row class="justify-content-center">
        <b-col lg="12">
          <b-card no-body>

            <el-table class="table-responsive table"
                      header-row-class-name="thead-light"
                      :data="contact"
            >
              <el-table-column label="N"
                               prop="budget"
                               min-width="140px">
                <template v-slot="{row}">
                  {{ row.id }}
                </template>
              </el-table-column>

              <el-table-column label="Nomi"
                               min-width="170px"
                               prop="status">
                <template v-slot="{row}">
                  <a href="#">
                    {{row.title}}
                  </a>
                </template>
              </el-table-column>


              <el-table-column label=""
                               prop="completion"
                               min-width="240px"
              >
                <template v-slot="{row}">
                  <div class="d-flex align-items-center">
                    <div>
                      <div class="text-center">
                        <router-link :to="`/tests/${row.id}/domains`"  target="_self" class="mr-2">
                          <b-icon icon="eye-fill" ></b-icon>
                        </router-link>
                      </div>
                    </div>
                  </div>
                </template>
              </el-table-column>
            </el-table>
            <div class="main_pagenation">

              <b-pagination
                v-model="currentPage"
                :total-rows="rows"
                :per-page="perPage"
                aria-controls="my-table"
                @input="updatePage(currentPage)"
              ></b-pagination>
            </div>
          </b-card>


        </b-col>
      </b-row>
