<script>
import {Table, TableColumn} from 'element-ui'
import axios from "axios";
import {baseUrl} from "../../../config";

export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      description:'',
      menus:[],
      rows: 1,
      perPage: 1,
      currentPage: 1,
      index: 0,
      right_t: false,
      data_main: [],
      title:'',
      order:'',
      isFooter:false,
      url:'',
      isButton:true,
      main_id:'',
      address:'',
      email_address:'',
      phone_number:'',
      telegram:'',
      instagram:'',
      facebook:'',
      contact:[],
      imageUrl:[],
      myImages:[],
      files_name:[],
    };
  },
  methods: {
    deletes(item) {
      let isBoss = confirm("You really want to delete?");
      if (isBoss == true) {
        axios.delete(`${baseUrl}profession-categories/${item.id}/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          })
          .then(() => {
            this.$toast("Ma'lumot o'chirildi", {
              timeout: 2000,
              type: "success"
            });
            axios.get(`${baseUrl}profession-categories/`)
              .then(res => {
                this.contact = res.data

              })
          })
      }
    },
    trySubmitFile(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    create(){
      const form = new FormData();
      // form.append("logo", this.myImages[0]);
      form.append("title", this.title);
      form.append("description", this.description);
      axios.post(`${baseUrl}profession-categories/`,form,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
        .then(()=>{
          axios.get(`${baseUrl}profession-categories/`)
            .then(res=>{
              this.contact=res.data
              this.phone_number=''
              this.email_address=''
              this.address=''
              this.facebook=''
              this.instagram=''
              this.telegram=''
            })
        })
    },
    edit(){
      axios.patch(`${baseUrl}company-info/${this.main_id}/`,{
          "title":this.title,
          "order":this.order,
          "url":this.url,
          "is_footer":this.isFooter
        },
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
        .then(()=>{
          axios.get(`${baseUrl}company-info/`)
            .then(res=>{
              this.menus=res.data
              this.isFooter=false
              this.url=''
              this.order=''
              this.title=''
              this.right_t =false
            })
        })
    },
    edit_main(row){
      this.main_id=row.id
      this.isButton=true
      this.isFooter=row.is_footer
      this.url=row.url
      this.order=row.order
      this.title=row.title
    }
  },
  mounted() {
    axios.get(`${baseUrl}tests`)
      .then(res=>{
        this.contact=res.data
        console.log(res)
      })
  }
}

</script>
